.App {
  background-color: aqua;
  height: 100vh;
  display: grid;
  place-items: center;
}

.app__videos {
  height: 100vh;
  max-height: 800px;
  width: 80%;
  max-width: 500px;
  border: 1px solid black;
  border-radius: 20px;
  overflow: scroll;
  scrollbar-width: none;
  -ms-overflow-style: none;
}

.app__videos::-webkit-scrollbar {
  display: none;
}


@media (max-width: 440px) {
  .app__videos {
    width: 100%;
  }
}